    //       root: "AgentRules",
    //       cols: ["RuleID AS Id", "RuleName AS Display"],
    //       con: [{ "ProductId": formvalue.ProductID }]
    //     }
    //   }else{
    //     col.config = {
    //       root: "AgentRules",
    //       cols: ["RuleID AS Id", "RuleName AS Display"],
    //     }
    //   }
    // }


    return fnRenderfrmControl(col, formvalue, handleChange, event)

